import uniRate from '@/components/uni-rate/uni-rate.vue'
export default {
	components: {
		uniRate
	},
	data() {
		return {
			userId:0,
			userName:"请选择客户",
			goodsId:0,
			goodsName:"请选择房源",
			rate:0,
			textArea:"",
			hasclickback:false
		}
	},
	onShow() {
		console.log(this.userId)
	},
	methods: {
		getRate(e){
			const that = this;
			that.rate=e.value
		},
		bindTextAreaBlur(e){
			this.textArea=e.detail.value;
		},
		sub(){
			const that = this;
			setTimeout(()=>{
				if(that.userId==0||that.goodsId==0||that.rate==0||that.textArea==""){
					uni.showToast({
						title:"请输入必填内容",
						icon:"none"
					})
				}
				else{
					
					uni.showModal({
						content: "日志提交后将不可再次修改，确认提交日志？",
						confirmText: "确认提交",
						cancelText: "取消",
						success: function (res) {
							if (res.confirm) {
								uni.showToast({
									title:"提交成功"
								})
							} else if (res.cancel) {
								
							}	
						}
					});	
					
				}
			},100)
		},
		searchCustomer(){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/searchCustomer"
			})
		},
		searchHouse(){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/searchHouse"
			})
		},
		back(){
			const that = this;
			if(that.userId!=0||that.goodsId!=0||that.rate!=0||that.textArea!=""){	
				uni.showModal({
						content: "退出后将不保留已编辑的内容，确认退出日志编辑？",
						confirmText: "继续编辑",
						cancelText: "退出",
					success: function (res) {
						if (res.confirm) {
				
						} else if (res.cancel) {
							that.$Router.back(1);
						}	
					}
				});		
			}
			else{
				that.$Router.back(1);
			}		
		}
	},
	
}
